import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App.vue'

Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      component: App,
      redirect: '/gauss',
      children: [{
        path: '/gauss',
        component: () => import(/* webpackChunkName: "gauss" */ '@/views/gauss/index.vue')
      },
      {
        path: '/cg',
        component: () => import(/* webpackChunkName: "cg" */ '@/views/cg/index.vue')
      }]
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})
